@use "../abstracts/globals" as globals;

.who-we-are {
  padding: 100px 0;

  .btn {
    @include globals.buttonTransition;

  }
  .lead {
    // Default mobile font size
    font-size: 1rem;

    // Tablet size
    @include globals.media-breakpoint-up(md) {
      font-size: 1.125rem;
    }

    // Desktop size
    @include globals.media-breakpoint-up(lg) {
      font-size: 1.25rem;
    }
  }
}
