@use "../abstracts/globals" as globals;

.footer-1 {
    position: relative;
    overflow: visible;
    padding-top: 50px;
    p{

        color: globals.$white;
        word-break: break-all;
    }

    .footer-cityscape {
        position: absolute;
        top: -30px;
        left: 0;
        width: 100%;
        height: 80px;
        z-index: 5;
        pointer-events: none;
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          
        }
    }

    .footer-nav-div {
        position: relative;
        z-index: 2;
        background-color: globals.$background-dark-1;
        color: globals.$background-light-1;
        padding-top: 100px;
        padding-bottom: 50px;

        .footer-brand {
            margin-bottom: 20px;

            img {
                max-width: 150px;
            }
        }

        .footer-text {
            color: globals.$light-color;
            margin-bottom: 20px;
        }

        h4 {
            color: globals.$theme-color;
            margin-bottom: 15px;
            position: relative;
            padding-bottom: 10px;

            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 50px;
                height: 2px;
                background-color: globals.$theme-color;
            }
        }

        .helpline h3 {
            color: globals.$white;
            font-size: 20px;
            margin-bottom: 15px;
            
    
            span {
                color: globals.$theme-color;
                display: inline-block;
            }
        }

        .footer-navigation {
            display: flex;;
            ul {
                list-style: none;
                padding: 0;
                margin: 0;
                flex-basis: 50%;

                li {
                    margin-bottom: 10px;

                    a {
                        color: globals.$light-color;
                        text-decoration: none;
                        transition: color 0.3s ease;

                        &:hover {
                            color: globals.$theme-color;
                        }
                    }
                }
            }
        }

        .app-download-box {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            gap: 15px;
            margin-bottom: 20px;

            h4 {
                margin-bottom: 10px;
                color: globals.$background-light-1;
                font-size: 16px;
                font-weight: 600;
                position: relative;
                padding-bottom: 5px;

                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 30px;
                    height: 2px;
                    background-color: globals.$theme-color;
                }
            }

            .download-links {
                display: flex;
                flex-wrap: wrap;
                gap: 10px;
                align-items: center;
                width: 100%;

                @include globals.respond('phone') {
                    justify-content: flex-start;
                }

                a {
                    display: inline-block;
                    max-width: 150px;
                    width: 100%;
                    transition: transform 0.3s ease;

                    @include globals.respond('phone') {
                        max-width: 120px;
                    }

                    img {
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }

                    &:hover {
                        transform: scale(1.05);
                    }
                }
            }
        }

        .cta-button {
            display: flex;
            flex-wrap: wrap;
            gap: 15px;
            align-items: center;

            a {
                width: 100%;
                display: block;
                border-radius: 50px;
                text-align: center;
                margin-bottom: 10px;
                border: 1px solid globals.$white;
                padding: 12px 18px;
                text-transform: uppercase;
                font-weight: 600;
                font-size: 12px;
                line-height: 1.2;
                transition: all 0.3s ease;

                @include globals.respond('phone') {
                    max-width: 100%;
                    padding: 10px 15px;
                    font-size: 11px;
                }

                &:hover {
                    background-color: globals.$white;
                    color: globals.$background-dark-2;
                }
            }
        }
    }

    .copyright-div {
        background-color: globals.$background-dark-2;
        color: globals.$light-color;
        padding: 20px 0;

        .social-nav {
            display: flex;
            justify-content: flex-end;
            margin: 0;
        
            @include globals.respond('tab') {
                justify-content: center;
                margin-top: 20px;
            }
        
            a {
                color: globals.$white;
                padding: 5px 12px;
                transition: all .1s;
                display: inline-block;
        
                &.facebook:hover {
                    color: globals.$facebook;
                }
        
                &.twitter:hover {
                    color: globals.$twitter;
                }
        
                &.instagram:hover {
                    color: globals.$instagram;
                }
        
                &.google-p:hover {
                    color: globals.$google-plus;
                }
        
                &.linkedin:hover {
                    color: globals.$linkedin;
                }
        
                &.pinterest:hover {
                    color: globals.$pinterest;
                }
            }
        }
    }
}
