@use "../abstracts/globals" as globals;

.faq-section {
    .faq-category-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .icon-box {
            transition: all 0.3s ease;
            
            i {
                transition: all 0.3s ease;
            }
        }

        &:hover .icon-box {
            transform: scale(1.1);
            
            i {
                transform: rotate(15deg);
            }
        }
    }

    .accordion {
        .accordion-item {
            background: transparent;
            
            .accordion-button {
                background: rgba(globals.$theme-color, 0.03);
                border: 1px solid rgba(0,0,0,0.1);
                font-weight: 500;
                padding: 1rem 1.5rem;
                
                &:not(.collapsed) {
                    color: globals.$theme-color-text;
                    background: rgba(globals.$theme-color, 0.1);
                    box-shadow: none;
                }
                
                &::after {
                    background-size: 1rem;
                    transition: all 0.3s ease;
                }
                
                &:hover {
                    color: globals.$theme-color-text;
                }
            }
            
            .accordion-body {
                padding: 1.5rem;
                background: white;
                border: 1px solid rgba(0,0,0,0.1);
                border-top: 0;
                border-radius: 0 0 0.5rem 0.5rem;
            }
        }
    }

    .still-questions {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);

            .icon-box {
                animation: bounce 0.5s;
            }
        }
    }
}

@keyframes bounce {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .faq-section {
        .accordion {
            .accordion-button {
                padding: 0.75rem 1rem;
                font-size: 0.9rem;
            }
            
            .accordion-body {
                padding: 1rem;
                font-size: 0.9rem;
            }
        }
        
        .still-questions {
            padding: 2rem !important;
        }
    }
}
