@use "../abstracts/globals" as globals;

.hero-area-v-1 {
    display: flex;
    position: relative;
    align-items: center;

    svg {
        position: absolute;
        width: 1400px;
        height: 750px;
        fill: #fff;
    }
    @include globals.respond('phone') {
        padding-top: 90px;
        svg {
            position: absolute;
            width: 300px;
            height: 750px;
            fill: #fff;
        }
    }

    .hero-area-slider {
        position: relative;

        .owl-dots {
            top: 115%;
            width: 100%;
            display: block;
            position: absolute;
            text-align: center;

            @include globals.respond('tab') {
                top: 96%;
            }

            button.owl-dot {
                width: 12px;
                height: 12px;
                background-color: transparent;
                margin: 5px;
                border-radius: 50px;
                border: 1px solid globals.$background-dark-1;
            }

            button.owl-dot.active {
                background-color: globals.$background-dark-2;
                border: 1px solid globals.$background-dark-2;
            }
        }

        .single-slider-item {
            display: flex;

            @include globals.respond('phone') {
                flex-wrap: wrap;
            }

            h1 {
                font-size: 60px;
                font-weight: 700;
                line-height: 66px;

                @include globals.respond('phone') {
                    font-size: 39px;
                    font-weight: 700;
                }
            }

            p {
                font-size: 18px;
                font-weight: 400;
                margin-top: 30px;
                margin-bottom: 45px;
                color: globals.$background-dark-1;
            }

            .hero-area-left {
                flex-basis: 40%;

                @include globals.respond('phone') {
                    flex-basis: 100%;
                    text-align: center;
                }
            }

            .hero-area-right {
                flex-basis: 60%;

                @include globals.respond('phone') {
                    flex-basis: 100%;
                }

                img {
                    @include globals.respond('tab') {
                        padding-top: 100px;
                    }

                    @include globals.respond('phone') {
                        padding-top: 50px;
                        padding-bottom: 50px;
                    }
                }
            }
        }
    }
}

.hero-bg-light-yellow {
    height: 630px;
    background-repeat: no-repeat;
    background-color: globals.$theme-color;
    background-position: center top;
}
.hero-bg-dark-yellow {
    z-index: 1;
    padding: 180px 0 250px;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: globals.$theme-color;
    background-position: center center;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 85%);
    &:before {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        content: '';
        z-index: -1;
        opacity: 0.75;
        position: absolute;
        background: #000000;
    }

    .hero-area-slider .owl-dots button.owl-dot {
        border: 1px solid globals.$theme-color;
    }
    .hero-area-slider .owl-dots button.owl-dot.active {
        background-color: globals.$theme-color;
        border: 1px solid globals.$theme-color;
    }
}

.hero-blob {
    position: absolute;
    top: -3px;
    left: -13%;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(360deg);
}
.hero-blob svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 750px;

    @include globals.respond('phone'){
        width:  calc(100% + 168px);
    }
}
@include globals.respond('tab'){
    .hero-area .hero-area-slider .single-slider-item .hero-area-left {
        flex-basis: 100%;
        text-align: center;
    }
    .hero-area .hero-area-slider .single-slider-item{
        flex-wrap: wrap;
    }
    .hero-area .hero-area-slider .single-slider-item .hero-area-right {
        flex-basis: 100%;
    }
}
.divider {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.divider svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 120px;
}

.divider .shape-fill {
    fill: #FFFFFF;
}
