.page-header {
    background-color: #fff;
    min-height: 400px; // Increased from 300px
    margin-top: 47px; // Added to match navigation height
    display: flex;
    align-items: center;

    .breadcrumb-item {
        font-size: 0.9rem;
        
        &.active {
            font-weight: 500;
        }
    }

    .blob-radius-2, .blob-radius-3 {
        filter: blur(40px);
        opacity: 0.8;
    }
}