@use "../abstracts/globals" as globals;
@use "sass:color";

.special-packages {
    .special-package-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-10px);
            box-shadow: 0 15px 30px rgba(0,0,0,0.1);

            .package-image {
                img {
                    transform: scale(1.1);
                }
            }
        }

        .package-image {
            height: 280px; // Fixed height for consistency
            
            img {
                width: 100%;
                height: 280px; // Match container height
                object-fit: cover;
                transition: all 0.5s ease;
                min-width: 420px; // Minimum width for quality
                max-width: 100%; // Ensure responsive behavior
            }

            .overlay-gradient {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(0deg, rgba(0,0,0,0.3) 0%, rgba(0,0,0,0) 100%);
            }

            .package-badge {
                .badge {
                    font-size: 0.9rem;
                    font-weight: 600;
                    box-shadow: 0 5px 15px rgba(globals.$warning, 0.3);
                }
            }
        }

        .package-content {
            h3 {
                font-size: 1.5rem;
                font-weight: 700;
            }

            .package-features {
                .feature-item {
                    i {
                        font-size: 1.1rem;
                    }
                }
            }

            .package-price {
                h4 {
                    font-size: 1.8rem;
                    font-weight: 700;
                    color: globals.$theme-color;
                }

                span {
                    font-size: 1.1rem;
                }
            }
        }
    }

    // Bottom CTA Box
    .cta-box {
        background: linear-gradient(145deg, #2c3e50, #1a252f);
        border: none;
        transition: all 0.3s ease;

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 15px 30px rgba(0,0,0,0.2);
            background: linear-gradient(145deg, #34495e, #2c3e50);
        }

        h3 {
            font-size: 2rem;
            font-weight: 700;
        }

        .btn {
            &:hover {
                transform: translateY(-2px);
            }
        }
    }
}

// Responsive Adjustments
@media (max-width: 991.98px) {
    .special-packages {
        .special-package-card {
            .package-image {
                height: 240px; // Slightly smaller for tablets

                img {
                    height: 240px;
                    min-width: 360px;
                }
            }

            .package-content {
                .feature-item {
                    justify-content: center;
                }

                .package-price {
                    justify-content: center;
                }
            }
        }
    }
}

@media (max-width: 767.98px) {
    .special-packages {
        .special-package-card {
            .package-image {
                height: 200px; // Smaller for mobile

                img {
                    height: 200px;
                    min-width: 300px;
                }
            }

            .package-content {
                text-align: center;

                .feature-item {
                    justify-content: center;
                }

                .package-price {
                    justify-content: center;
                }
            }
        }
    }
}
