@use "../abstracts/globals" as globals;

.packages-hero {
    .hero-content {
        .badge {
            font-size: 0.9rem;
            border: 1px solid rgba(globals.$warning, 0.3);
        }

        .features-list {
            .icon-box {
                width: 48px;
                height: 48px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.3s ease;

                i {
                    font-size: 1.25rem;
                    transition: all 0.3s ease;
                }
            }

            .d-flex {
                &:hover {
                    .icon-box {
                        transform: scale(1.1);

                        i {
                            transform: rotate(15deg);
                        }
                    }
                }
            }
        }
    }

    .hero-image {
        .main-image {
            position: relative;
            z-index: 1;
            transition: all 0.3s ease;

            &:hover {
                transform: translateY(-10px);
            }

            img {
                width: 100%;
                height: auto;
            }
        }

        .position-absolute {
            z-index: 2;
            transition: all 0.3s ease;

            .icon-box {
                width: 48px;
                height: 48px;
                display: flex;
                align-items: center;
                justify-content: center;

                i {
                    font-size: 1.25rem;
                }
            }

            &:hover {
                transform: translate(-20%, 20%) scale(1.05);

                &:first-child {
                    transform: translate(20%, -20%) scale(1.05);
                }
            }
        }
    }
}

// Blob shapes
.blob-radius-2 {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
}

.blob-radius-3 {
    border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
}

// Responsive adjustments
@media (max-width: 991.98px) {
    .packages-hero {
        .hero-image {
            margin-top: 3rem;

            .position-absolute {
                position: relative !important;
                transform: none !important;
                margin: 1rem 0;
                width: fit-content;

                &:first-child {
                    margin-left: auto;
                }
            }
        }
    }
}

@media (max-width: 575.98px) {
    .packages-hero {
        .features-list {
            .col-sm-6 {
                margin-bottom: 1rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .d-flex.flex-wrap {
            .btn {
                width: 100%;
            }
        }
    }
}
