@use "../abstracts/globals" as globals;
@use "sass:color";

.cta-section {
    .cta-main-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(255,255,255,0.1);
        // background: linear-gradient(45deg, globals.$theme-color, color.scale(globals.$theme-color, $lightness: -15%));

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 15px 30px rgba(0,0,0,0.2);

            .icon-box {
                background-color: rgba(255,255,255,0.2);
            }
        }

        .cta-features {
            .icon-box {
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.3s ease;
            }
        }
    }

    .quick-contact-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);

            .icon-box {
                animation: shake 0.5s;
            }
        }

        .icon-box {
            transition: all 0.3s ease;
        }
    }

    .booking-process-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .process-steps {
            .step-number {
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-weight: bold;
            }

            .process-step {
                position: relative;

                &:not(:last-child)::after {
                    content: '';
                    position: absolute;
                    left: 15px;
                    top: 40px;
                    width: 2px;
                    height: calc(100% - 20px);
                    background-color: rgba(globals.$theme-color, 0.2);
                }
            }
        }
    }

    .special-offer-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(globals.$warning, 0.3);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(globals.$warning, 0.2);

            .icon-box {
                animation: tada 1s;
            }
        }
    }
}

@keyframes shake {
    0%, 100% { transform: rotate(0); }
    25% { transform: rotate(-10deg); }
    75% { transform: rotate(10deg); }
}

@keyframes tada {
    0% { transform: scale(1) rotate(0); }
    10%, 20% { transform: scale(0.9) rotate(-3deg); }
    30%, 50%, 70%, 90% { transform: scale(1.1) rotate(3deg); }
    40%, 60%, 80% { transform: scale(1.1) rotate(-3deg); }
    100% { transform: scale(1) rotate(0); }
}

// Responsive adjustments
@media (max-width: 768px) {
    .cta-section {
        .cta-main-card {
            padding: 2rem !important;
        }

        .quick-contact-card {
            .row {
                text-align: center;
                
                > * {
                    margin-bottom: 1rem;
                    
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }

        .special-offer-card {
            .row {
                text-align: center;
                
                > * {
                    margin-bottom: 1rem;
                    
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}
