@use "sass:map"; 
@mixin clearfix {
    &::after {
        content: '';
        display: table;
        clear: both;
    } 
}

@mixin centerElement{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

@mixin backgroundimg {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

@mixin inputPlaceholder() {
	$selector: '';
	$prefixes: (moz: "::-moz",
					webkit: "::-webkit",
					ie: ":-ms"
	);

	@each $prop,
	$value in $prefixes {
		@if $prop !="moz" {
			$selector: #{$value}-input-placeholder;
		}

		@else {
			$selector: #{$value}-placeholder;
		}

		@if & {
			&#{$selector} {
				@content;
			}
		}

		@else {
			#{$selector} {
				@content;
			}
		}
	}

	&::placeholder {
		@content;
	}
}

@mixin sm {

	/* Small devices */
	@media (max-width: #{$sm}) {
		@content;
	}
}

//button transition effect 

@mixin buttonTransition($duration: 0.3s, $timing: ease-in-out) {
  transition: all #{$duration} #{$timing};
  
  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  }
  
  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 5px rgba(0,0,0,0.05);
  }
}

//MEDIA QUERY MANAGER

/*
0 - 768: Phone
600 - 900: Tablet portrait
900 - 1200: Tablet landscape
1200 - 1800: Normal styles
1800+ : Big Desktop

1em = 16px

The smaller device rules always should write below the bigger device rules

Fixing Order => Base + Typography >> General Layout + Grid >> Page Layout + Component
*/

@mixin respond($breakpoint) {

	@if($breakpoint == "phone"){ 

		@media only screen and (max-width: 575px) {@content;} 
	}

	@if($breakpoint == "phone-land"){ 

		@media only screen and (max-width: 767px) {@content;} 
	}

	@if($breakpoint == "tab"){

		@media only screen and (max-width: 991px) {@content;}
	}

	@if($breakpoint == "desktop"){

		@media only screen and (max-width: 1200px) {@content;} 
	}
	
	@if($breakpoint == "big-desktop"){

		@media only screen and (min-width: 112.5em) {@content;} //1800px
	}

}

@mixin media($min, $max) {
    @media only screen and (min-width: $min) and (max-width: $max) {@content;} 
}

// Grid breakpoints for future use, old ones will become redundant in coming updates
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;

@mixin media-breakpoint-up($breakpoint, $breakpoint-map: $grid-breakpoints) {
  $min: map.get($breakpoint-map, $breakpoint);
  @if $min != 0 {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

@mixin media-breakpoint-down($breakpoint, $breakpoint-map: $grid-breakpoints) {
  $max: map.get($breakpoint-map, $breakpoint);
  @if $max != null {
    @media (max-width: ($max - .02px)) {
      @content;
    }
  } @else {
    @content;
  }
}

@mixin media-breakpoint-between($lower, $upper, $breakpoint-map: $grid-breakpoints) {
  $min-lower: map.get($breakpoint-map, $lower);
  $max-upper: map.get($breakpoint-map, $upper);

  @media (min-width: $min-lower) and (max-width: ($max-upper - .02px)) {
    @content;
  }
}

@mixin media-breakpoint-only($breakpoint, $breakpoint-map: $grid-breakpoints) {
  $min-breakpoint: map.get($breakpoint-map, $breakpoint);
  $next-breakpoint: map.get($breakpoint-map, "#{$breakpoint}-up");

  @if $next-breakpoint {
    @media (min-width: $min-breakpoint) and (max-width: ($next-breakpoint - .02px)) {
      @content;
    }
  } @else {
    @media (min-width: $min-breakpoint) {
      @content;
    }
  }
}