@use "../abstracts/globals" as globals;

.cta-packages {
    background: linear-gradient(145deg, #2c3e50, #1a252f);
    border: none;
    transition: all 0.3s ease;
    .cta-content {

        &:hover {
            transform: translateY(-5px);
           
        }

        .badge {
            font-size: 0.9rem;
            font-weight: 600;
            box-shadow: 0 5px 15px rgba(globals.$warning, 0.3);
        }

        .btn {
            transition: all 0.3s ease;
            
            &:hover {
                transform: translateY(-2px);
            }

            &.btn-lg {
                padding: 0.8rem 2rem;
            }
        }

        .rounded-circle {
            transition: all 0.5s ease;
            opacity: 0.5;
        }

        &:hover {
            .rounded-circle {
                transform: scale(1.1) rotate(5deg);
            }
        }
    }
}

// Responsive Adjustments
@media (max-width: 767.98px) {
    .cta-packages {
        .cta-content {
            .display-4 {
                font-size: 2.5rem;
            }

            .btn-lg {
                width: 100%;
                margin-bottom: 1rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
