@use "../abstracts/globals" as globals;
.vehicle-tab-nav {
    
    ul {
        border: 0;
        text-align: center;
        margin-bottom: 50px;
        justify-content: center;

        a {
            padding: 6px 16px;
            display: inline-block;
            color: globals.$body-font-color;

            &.active {
                background-color: globals.$theme-color;
                color: globals.$background-light-1;
            }
        }
    }
}