@use "../abstracts/globals" as globals;

.counter-wrapper {
    display: flex;
    justify-content: space-around;
    padding: 50px 0;

    @include globals.respond('phone') {
        flex-wrap: wrap;
        padding: 30px 0;
    }
}

.single-counter-box {
    flex-basis: 25%;
    text-align: center;

    @include globals.respond('phone') {
        flex-basis: 50%;
    }
}

.vahicles-container {

    .row {
        margin-top: 30px;
    }
}

.single-vehicle-container {
    // border: 1px solid rgba($color: globals.$background-dark-1, $alpha: .1);
    // background-color: globals.$background-dark-3;
    // padding: 75px 20px;
    margin-bottom: 30px;
    text-align: center;
}

.single-driver .card {
    background-color: globals.$background-dark-3;
    border-radius: 0;
    margin-bottom: 25px;
}

.tab-dashboard {
    @include globals.respond('phone') {
        margin-top: 15px;
    }
}

.rides-filter ul li {
    list-style: none;
    display: inline-block;

    a {
        display: inline-block;
        color: globals.$background-dark-1;
        padding: 5px 15px;
        border: 1px solid transparent;
        transition: all .1s;

        @include globals.respond('phone') {
            padding: 5px 10px;
        }

        &:hover {
            border-color: rgba($color: globals.$background-dark-1, $alpha: .3);
        }
    }
}

.tab-dashboard {

    .tab-navigation {
        margin-bottom: 45px;
    }

    .tab-navigation a {
        display: inline-block;
        padding: 15px;
        border: 1px solid transparent;
        border-bottom: none;
        color: globals.$body-font-color;

        @include globals.respond('phone') {
            padding: 8px;
        }
    }

    .tab-navigation a.active {
        background-color: globals.$background-dark-3;
        color: globals.$background-dark-1;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        border: 1px solid rgba($color: globals.$background-dark-1, $alpha: .1);
        border-bottom: none;
    }
}

.tab-pane {

    h4 {
        margin-bottom: 30px;
    }

    .small-div-item {
        background-color: globals.$background-dark-3;
    }

    .small-div:not(:last-child) {
        margin-bottom: 50px;
    }
}


.driver-dashboard-div {
    .right-text {
        text-align: right;

        @include globals.respond('phone') {
            text-align: left;
        }
    }
}

.div-heading {
    display: flex;

    .heading-item {
        flex-basis: 50%;
        width: 100%;

        @include globals.respond('phone') {
            &-1 {
                flex-basis: 80%;
            }

            &-2 {
                flex-basis: 20%;
            }
        }

        &.right {
            display: flex;
            justify-content: flex-end;
            color: globals.$heading-font-color;
        }
    }
}
.media {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
}
.media-body {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.dashboard-section {
    padding: 90px 0;
    background-color: globals.$background-light-2;

    .dashboard-sidebar {
        background-color: globals.$white;
        border: 1px solid globals.$border;
        border-radius: 10px;
        padding: 30px 0;
        margin-bottom: 30px;

        .sidebar-nav {
            li {
                margin-bottom: 10px;

                a {
                    display: block;
                    padding: 10px 20px;
                    color: globals.$title-color;
                    transition: all 0.3s ease;

                    &:hover,
                    &.active {
                        background-color: globals.$theme-color;
                        color: globals.$white;
                        border-radius: 5px;
                    }
                }
            }
        }
    }

    .dashboard-content {
        background-color: globals.$white;
        border: 1px solid globals.$border;
        border-radius: 10px;
        padding: 30px;

        .content-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 30px;

            h2 {
                margin: 0;
                color: globals.$title-color;
                font-size: 24px;
            }

            .header-btn {
                display: flex;
                align-items: center;

                a {
                    margin-left: 15px;
                    padding: 10px 20px;
                    background-color: globals.$theme-color;
                    color: globals.$white;
                    border-radius: 5px;
                    transition: all 0.3s ease;

                    &:hover {
                        background-color: globals.$theme-color-darker;
                    }
                }
            }
        }

        .dashboard-table {
            width: 100%;
            border-collapse: collapse;

            thead {
                background-color: globals.$background-dark-3;

                th {
                    padding: 15px;
                    text-align: left;
                    color: globals.$title-color;
                    font-weight: 600;
                    border-bottom: 1px solid globals.$border;
                }
            }

            tbody {
                tr {
                    border-bottom: 1px solid globals.$border;

                    &:last-child {
                        border-bottom: none;
                    }

                    td {
                        padding: 15px;
                        color: globals.$body-font-color;

                        a {
                            color: globals.$theme-color;
                            transition: color 0.3s ease;

                            &:hover {
                                color: globals.$theme-color-darker;
                            }
                        }
                    }
                }
            }
        }
    }
}