@use "../abstracts/globals" as globals;

.vehicle-categories {
    .category-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);

            .category-image img {
                transform: scale(1.05);
            }
        }

        .category-image {
            position: relative;
            border-radius: 1rem;
            overflow: hidden;

            img {
                transition: transform 0.3s ease;
                width: 100%;
                height: auto;
            }

            .category-overlay {
                position: absolute;
                top: 1rem;
                right: 1rem;
                
                .badge {
                    font-size: 0.875rem;
                    padding: 0.5rem 1rem;
                }
            }
        }

        .category-icon {
            width: 50px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.25rem;
        }

        .category-features {
            .feature-item {
                padding: 0.5rem;
                border-radius: 0.5rem;
                background: rgba(globals.$theme-color, 0.05);
                font-size: 0.875rem;
                transition: all 0.3s ease;

                &:hover {
                    background: rgba(globals.$theme-color, 0.1);
                    transform: translateX(5px);
                }
            }
        }

        .btn {
            transition: all 0.3s ease;
            
            &:hover {
                transform: translateY(-2px);
            }
        }
    }

    .blob-radius-2 {
        border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }

    .blob-radius-3 {
        border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
    }
}