@use "../abstracts/globals" as globals;
.testimonial-2{
  position: relative;
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: center top;
  background-image: url(../../assets/images/home/bg3.webp);
  z-index:1;
    .testimonial-item{
        box-shadow: 0px 8px 22px -9px rgba(66, 68, 90, 1);
        i.fas{
            color: globals.$dark;
        }
    }
    .swiper-pagination {
        position: relative;
        margin-top: 2rem; // Add space between slides and pagination
        display: flex;
        justify-content: center;
        align-items: center;
    
        &-bullet {
          width: 12px;
          height: 12px;
          background-color: #ddd;
          opacity: 0.5;
          margin: 0 8px; // Space between pagination dots
          transition: all 0.3s ease;
          border-radius: 50%;
    
          &-active {
            width: 16px;
            height: 16px;
            background-color: var(--primary-color, #ffc107); // Use your theme's primary color
            opacity: 1;
          }
        }
    }
}
