@use "../abstracts/globals" as globals;

.testimonials-section {
    .testimonial-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);

            .testimonial-vehicle {
                background-color: rgba(globals.$theme-color, 0.1) !important;
            }
        }

        .testimonial-rating {
            font-size: 1.1rem;
        }

        .testimonial-text {
            position: relative;
            min-height: 100px;
            
            p {
                font-size: 1rem;
                line-height: 1.6;
            }

            &::before {
                content: '"';
                font-size: 4rem;
                font-family: serif;
                color: rgba(globals.$theme-color, 0.1);
                position: absolute;
                top: -2rem;
                left: -1rem;
                z-index: 0;
            }
        }

        .testimonial-vehicle {
            transition: all 0.3s ease;
            
            i {
                font-size: 1.5rem;
            }
        }

        .testimonial-author {
            img {
                border: 2px solid globals.$theme-color;
                padding: 2px;
                object-fit: cover;
                box-shadow: 0 3px 10px rgba(0,0,0,0.1);
            }

            .ms-3 {
                h6 {
                    margin-bottom: 0.25rem;
                }
            }
        }
    }

    .trust-indicator {
        transition: all 0.3s ease;
        padding: 2rem;
        border-radius: 1rem;
        background: rgba(globals.$theme-color, 0.03);

        &:hover {
            transform: translateY(-5px);
            background: rgba(globals.$theme-color, 0.05);
        }

        .counter {
            font-weight: 700;
        }
    }

    .blob-radius-2 {
        border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }

    .blob-radius-3 {
        border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .testimonials-section {
        .testimonial-card {
            .testimonial-text {
                min-height: auto;
                p {
                    font-size: 0.9rem;
                }
            }

            .testimonial-author {
                img {
                    width: 60px;
                    height: 60px;
                }
            }
        }

        .trust-indicator {
            padding: 1.5rem;
        }
    }
}
