@use "../abstracts/globals" as globals;

.single-contact-info {
    padding: 27px;
    border: 1px solid rgba($color: globals.$background-dark-2, $alpha: .2);
    min-height: 225px;
    border-radius: 5px;

    @include globals.respond('tab') {
        margin-bottom: 20px;
        min-height: 250px;
    }

    img {
        margin-bottom: 20px;
    }
}