// SCSS for Ride Booking Partial
.ride-booking-section {
  // Variables
  $primary-color: #4361ee;
  $secondary-color: #3f37c9;
  $text-color: #212529;
  $light-gray: #e9ecef;
  $border-radius: 12px;
  
  // Booking Form
  .booking-form-card {
    background-color: #ffffff;
    border-radius: $border-radius;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    padding: 1.5rem;
    margin-bottom: 1rem;
    
    form {
      display: flex;
      flex-direction: column;
      gap: 1.25rem;
    }
  }
  
  // Location inputs
  .location-input {
    margin-bottom: 0.5rem;
    
    label {
      display: block;
      margin-bottom: 0.5rem;
      font-weight: 500;
      color: $text-color;
    }
    
    .input-with-icon {
      position: relative;
      
      i {
        position: absolute;
        left: 12px;
        top: 50%;
        transform: translateY(-50%);
        color: $primary-color;
      }
      
      input {
        padding-left: 35px;
        border-radius: 8px;
        border: 1px solid $light-gray;
        height: 45px;
        
        &:focus {
          border-color: $primary-color;
          box-shadow: 0 0 0 0.2rem rgba(67, 97, 238, 0.25);
        }
      }
    }
  }
  
  // Payment section
  .payment-section {
    h2 {
      font-size: 1.125rem;
      font-weight: 600;
      margin-bottom: 1rem;
    }
    
    .payment-options {
      display: flex;
      gap: 1rem;
      flex-wrap: wrap;
      
      .form-check {
        min-width: 100px;
        background-color: #f8f9fa;
        border-radius: 8px;
        padding: 0.75rem 1rem;
        cursor: pointer;
        transition: all 0.2s ease;
        
        &:hover {
          background-color: #f1f3f5;
        }
        
        input:checked + label {
          color: $primary-color;
          font-weight: 500;
        }
      }
    }
  }
  
  // Car selection
  .car-selection-section {
    h2 {
      font-size: 1.125rem;
      font-weight: 600;
      margin-bottom: 1rem;
    }
    
    .car-options {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      gap: 0.5rem;
      overflow-x: hidden;
      justify-content: space-between;
      padding: 0.5rem 0;
      
      &.horizontal-layout {
        .car-option {
          flex: 0 0 auto;
          width: 19%;
          margin-bottom: 0.75rem;
          
          .car-label.horizontal {
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            text-align: left;
            padding: 0.75rem 0.5rem;
            
            .car-left {
              display: flex;
              align-items: center;
              margin-right: 0.5rem;
            }
            
            .car-image {
              margin-bottom: 0;
              width: 50px;
              height: 50px;
            }
            
            .car-details {
              text-align: left;
              
              h4 {
                margin-bottom: 0.25rem;
                font-size: 1rem;
              }
              
              p {
                font-size: 0.8rem;
              }
            }
          }
        }
      }
      
      .car-option {
        position: relative;
        flex: 1 0 14%;
        max-width: 14%;
        display: flex;
        flex-direction: column;
        
        @media (max-width: 767px) {
          flex: 1 0 calc(33.333% - 1rem);
          max-width: calc(33.333% - 1rem);
        }
        
        input {
          position: absolute;
          opacity: 0;
          
          &:checked + .car-label {
            border-color: $primary-color;
            background-color: rgba(67, 97, 238, 0.1);
            box-shadow: 0 3px 10px rgba(67, 97, 238, 0.2);
            transform: translateY(-2px);
            
            
            
            .car-details {
              h4, p {
                color: $primary-color;
              }
            }
          }
          
          .car-details {
            text-align: center;
            margin-top: 0.75rem;
            
            h4 {
              font-size: 1.25rem;
              margin: 0 0 0.25rem 0;
              font-weight: 600;
            }
            
            p {
              margin: 0;
              font-size: 0.9rem;
              color: #6c757d;
              font-weight: 500;
            }
          }
        }
        
        .car-label {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.75rem;
          padding: 1rem;
          border: 2px solid $light-gray;
          border-radius: 12px;
          cursor: pointer;
          transition: all 0.2s ease;
          
          &:hover {
            border-color: #ced4da;
            transform: translateY(-2px);
            box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
          }
          
          .car-image {
            width: 100px;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0.5rem;
            
            img {
              max-width: 100%;
              height: auto;
              filter: drop-shadow(0 2px 3px rgba(0, 0, 0, 0.2));
            }
          }
        }
      }
    }
  }
  
  // Book now button
  .book-now-btn {
    background: linear-gradient(135deg, $primary-color, $secondary-color);
    color: white;
    border: none;
    padding: 0.75rem 2rem;
    border-radius: 8px;
    font-weight: 600;
    margin-top: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    
    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 5px 15px rgba($primary-color, 0.3);
    }
  }
  
  // Map area
  .ride-map-area {
    position: relative;
    height: 660px; /* Explicitly set height to match _google-map.scss */
    border-radius: $border-radius;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    
    .mapouter, .gmap_canvas {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
    
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 0;
    }
  }
  
  // Responsive adjustments
  @media (max-width: 991.98px) {
    .ride-map-area {
      margin-top: 1rem;
      height: 500px; /* Reduced height for tablets */
    }
  }
  
  @media (max-width: 767.98px) {
    .ride-map-area {
      height: 400px; /* Further reduced height for mobile phones */
      min-height: 350px;
    }
  }
}