@use "../abstracts/globals" as globals;
@use "sass:color";

.register-section {
    background-color: globals.$background-light-3;
    min-height: calc(100vh - 400px);
    display: flex;
    align-items: center;

    .register-card {
        transition: transform 0.3s ease;

        &:hover {
            transform: translateY(-5px);
        }

        .nav-pills {
            .nav-link {
                color: globals.$text-dark-color;
                border-radius: 50rem;
                padding: 0.6rem;
                transition: all 0.3s ease;
                
                &:hover {
                    background-color: color.adjust(globals.$theme-color, $alpha: -0.9);
                }
                
                &.active {
                    background-color: globals.$theme-color;
                    color: globals.$black;
                    font-weight: 700;
                }
            }
        }

        .form-floating {
            .form-control,
            .form-select {
                border: 1px solid globals.$form-control-border;
                
                &:focus {
                    box-shadow: 0 0 0 0.25rem rgba(globals.$theme-color, 0.25);
                    border-color: globals.$theme-color;
                }
            }
        }

        

        

        .form-check-input:checked {
            background-color: globals.$theme-color;
            border-color: globals.$theme-color;
        }

        textarea.form-control {
            min-height: 100px;
        }

        .social-register {
            position: relative;
            
            &:before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                right: 0;
                height: 1px;
                background-color: globals.$form-control-border;
                z-index: 1;
            }
            
            p {
                position: relative;
                z-index: 2;
                display: inline-block;
                padding: 0 1rem;
                background-color: globals.$white;
            }

            .btn-light {
                width: 40px;
                height: 40px;
                padding: 0;
                line-height: 40px;
                transition: all 0.3s ease;
                z-index: 2;


                &.facebook:hover {
                    color: globals.$facebook;
                    background-color: globals.$bg-social;
                }
        
                &.google:hover {
                    color: globals.$google-plus;
                    background-color: globals.$bg-social;
                }
        
                &.twitter:hover {
                    color: globals.$twitter;
                    background-color: globals.$bg-social;
                }
            }
        }
    }
}
