@use "../abstracts/globals" as globals;

.hero-area-1 {
  .card {
    border-radius: 16px;
    overflow: hidden; // Ensures rounded corners are respected

    .card-header {
      margin-bottom: -1px; // Removes gap between header and body
      background-color: globals.$background-dark-3;
      padding: 0; // Remove default padding
      border: none; // Remove default border
    }

    .card-body {
      border-top-left-radius: 16px;
      border-top-right-radius: 16px;
    }
  }

  .form-tab {
    background-color: transparent; // Remove background from tab
    display: flex;
    
    .nav-item {
      flex: 1;
      position: relative;
    }

    .nav-link {
      color: globals.$muted;
      font-weight: 500;
      padding: 15px 10px;
      transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
      text-align: center;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(globals.$light-color, 5%);
      
      &#drive-tab {
        border-top-left-radius: 0;
        border-top-right-radius: 16px;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }

      &#ride-tab {
        border-top-left-radius: 16px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }
      
      &:hover {
        background-color: rgba(globals.$theme-color, 0.1);
      }

      &.active {
        color: globals.$black;
        font-weight: 700;
        background-color: globals.$white;
        box-shadow: 0 -4px 6px rgba(globals.$black, 0.08);
        i {
          color: globals.$theme-color; // Make icon yellow
          margin-right: 8px; // Add some spacing
        }

        &#drive-tab {
          border-top-left-radius: 0;
          border-top-right-radius: 16px;
          border-bottom-right-radius: 0;
          border-bottom-left-radius: 0;
        }

        &#ride-tab {
          border-top-left-radius: 16px;
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
          border-bottom-left-radius: 0;
        }
      }
    }
  }

  // Smooth content transition
  .tab-content {
    .tab-pane {
      transition: opacity 0.5s ease, transform 0.5s ease;
      opacity: 0;
      transform: translateY(10px);

      &.active {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
   // Custom button styling
   .download-buttons {
    .btn {
      transition: all 0.3s ease;
      
      &:hover {
        transform: translateY(-2px);
      }
    }
  }
}