@use "../abstracts/globals" as globals;

.blog-listing-area {
    
    .card {
        overflow: hidden;
        
        &-img-top {
            height: 240px;  /* Perfect 2:1 ratio from original 480px */
            width: 100%;
            object-fit: cover;
            object-position: center;  /* Ensures image is centered when cropped */
            aspect-ratio: 3/2;  /* Maintains consistent aspect ratio */
            transition: transform 0.3s ease;
        }
        
        &:hover {
            .card-img-top {
                transform: scale(1.05);
            }
        }
        
        .author {
            a {

                color: globals.$theme-color-text;
            }
        }
    }

/*--------------------------------------------------------------
  # Pagination
  --------------------------------------------------------------*/
    .pagination {
        display: block;
        text-align: center;

        li {
            display: inline-block;
            margin-right: 15px;

            &.next a,
            &.prev a {
                width: auto;
                height: auto;
                border: 0;
            }

            &:last-child {
                margin-right: 0;
            }

            a {
                display: inline-block;
                color: globals.$title-color;
                font-size: 16px;
                text-align: center;
                border: 0;
                font-weight: 400;
                padding: 0;
                height: 45px;
                text-transform: uppercase;
                width: 45px;
                line-height: 45px;
                border: 1px dashed transparent;
                border-radius: 0;
                border-radius: 50%;

                &:hover {
                    background-color: inherit;
                    color: globals.$black;
                }
            }

            &.active a {
                border-color: globals.$black;
                background-color: inherit;
                color: globals.$black;
                font-weight: 700;
            }
        }
    }
}