@use '../abstracts/variables' as *;

.testimonials-self-driving {
  position: relative;
  
  // Tech background elements
  .tech-background {
    z-index: -1;
    overflow: hidden;
    
    .tech-grid-light {
      background-image: linear-gradient(to right, rgba($theme-color, 0.05) 1px, transparent 1px),
                        linear-gradient(to bottom, rgba($theme-color, 0.05) 1px, transparent 1px);
      background-size: 40px 40px;
      opacity: 0.7;
    }
    
    .tech-circle {
      border-radius: 50%;
      
      &.tech-circle-1 {
        width: 300px;
        height: 300px;
        top: -100px;
        right: -100px;
        opacity: 0.6;
      }
      
      &.tech-circle-2 {
        width: 400px;
        height: 400px;
        bottom: -150px;
        left: -150px;
        opacity: 0.4;
      }
    }
  }
  
  // Video testimonial styling
  .testimonial-video {
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    
    &:hover {
      transform: translateY(-5px);
      
      .play-icon {
        transform: scale(1.1);
      }
    }
    
    img {
      transition: all 0.5s ease;
    }
    
    // Video play button
    .video-play-button {
      text-decoration: none;
      z-index: 2;
      
      .play-icon {
        width: 80px;
        height: 80px;
        transition: all 0.3s ease;
        box-shadow: 0 0 30px rgba($theme-color, 0.5);
        
        i {
          font-size: 24px;
          margin-left: 4px; // Slight offset for play icon
        }
      }
    }
    
    // Overlay effect
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0,0,0,0.1), rgba(0,0,0,0.3));
      z-index: 1;
    }
  }
  
  // Testimonial content styling
  .testimonial-content {
    h2 {
      position: relative;
      
      &::after {
        content: '';
        position: absolute;
        bottom: -15px;
        left: 0;
        width: 60px;
        height: 3px;
        background-color: $theme-color;
      }
    }
    
    p {
      color: #555;
      line-height: 1.8;
    }
  }
  
  // Author styling
  .testimonial-author {
    .author-info {
      .author-name {
        color: #333;
      }
      
      .author-title {
        font-size: 0.95rem;
      }
    }
  }
}
