@use "../abstracts/globals" as globals;

.pricing-section {
    .pricing-tabs {
        background: rgba(globals.$theme-color, 0.5);
        padding: 0.5rem;
        border-radius: 100px;
        display: inline-flex;
        
        .btn {
            border-radius: 100px;
            font-weight: 500;
            padding: 0.75rem 1.5rem;
            transition: all 0.3s ease;
            border: 1px solid transparent;
            
            &.active {
                background: globals.$theme-color-darker;
                color: globals.$theme-color-text;
                box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
            }
            
            &:not(.active):hover {
                color: white;
                background: rgba(255, 255, 255, 0.1);
            }

            .badge {
                transform: translateY(-1px);
            }
        }
    }
    .bg-gradient-dark {
        background: linear-gradient(145deg, #2c3e50, #1a252f) !important;
        border: none;
        
        &:hover {
            background: linear-gradient(145deg, #34495e, #2c3e50) !important;
        }
    }

    .pricing-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .pricing-icon {
            width: 70px;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .pricing-amount {
            .currency {
                font-size: 1.5rem;
                vertical-align: super;
                margin-right: 0.25rem;
            }
            
            .period {
                font-size: 1rem;
                margin-left: 0.25rem;
            }
        }

        .pricing-features {
            li {
                transition: all 0.3s ease;
                
                &:hover {
                    transform: translateX(5px);
                }
            }
        }

        &.bg-theme-color {
            .btn-light {
                transition: all 0.3s ease;
                
                &:hover {
                    background-color: transparent;
                    color: white;
                    border-color: white;
                }
            }
        }
    }

    .popular-badge {
        font-size: 0.875rem;
        font-weight: 500;
    }

    .tab-content {
        .tab-pane {
            transition: all 0.3s ease;
            
            &.fade {
                transform: translateY(10px);
                opacity: 0;
            }
            
            &.show {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}