@use "../abstracts/globals" as globals;
@use "sass:color";

.contact-section {
    background-color: globals.$background-light-3;

    .contact-info {
        transition: transform 0.3s ease;

        &:hover {
            transform: translateY(-5px);
        }

        .contact-card {
            transition: all 0.3s ease;

            &:hover {
                background-color: globals.$theme-color !important;
                
                .contact-icon i,
                .contact-details h5,
                .contact-details p {
                    color: globals.$black !important;
                }
            }
        }
    }

    .contact-form {
        .form-control {
            border: 1px solid globals.$form-control-border;
            
            &:focus {
                box-shadow: 0 0 0 0.25rem rgba(globals.$theme-color, 0.25);
                border-color: globals.$theme-color;
            }
        }

        .btn-primary {
            background-color: globals.$theme-color;
            border-color: globals.$theme-color;
            transition: all 0.3s ease;

            &:hover {
                background-color: color.adjust(globals.$theme-color, $lightness: -10%);
                border-color: color.adjust(globals.$theme-color, $lightness: -10%);
                transform: translateY(-2px);
            }
        }
    }

    .social-links {
        .btn-light {
            width: 40px;
            height: 40px;
            transition: all 0.3s ease;

            &.facebook:hover {
                color: globals.$facebook;
                background-color: globals.$bg-social;
            }
    
            &.twitter:hover {
                color: globals.$twitter;
                background-color: globals.$bg-social;
            }
    
            &.instagram:hover {
                color: globals.$instagram;
                background-color: globals.$bg-social;
            }
    
            &.google-p:hover {
                color: globals.$google-plus;
                background-color: globals.$bg-social;
            }
    
            &.linkedin:hover {
                color: globals.$linkedin;
                background-color: globals.$bg-social;
            }
    
            &.pinterest:hover {
                color: globals.$pinterest;
                background-color: globals.$bg-social;
            }
        }
    }
}

.map-container {
    overflow: hidden;
    position: relative;
    
    iframe {
        filter: grayscale(1);
        transition: filter 0.3s ease;

        &:hover {
            filter: grayscale(0);
        }
    }
}
