@use "../abstracts/globals" as globals;

.footer-1, .footer-2 {
    #gmap_canvas {
        width: 100%;
        height: 340px;

        @include globals.respond('tab') {
            height: 200px;
        }
    }
}

.ride-map-area #gmap_canvas,
.contact-us-map #gmap_canvas {
    width: 100%;
    height: 100%; /* Changed to 100% to inherit from parent container */

    @include globals.respond('phone') {
        height: 500px;
    }
}
.contact-us-map {
    @include globals.respond('tab') {
        margin-top: 20px;
    }
}

.ride-map #gmap_canvas {
    width: 100%;
    height: 730px;

    @include globals.respond('tab') {
        height: 500px;
    }

    @include globals.respond('phone') {
        height: 400px;
    }
}