.counter-section {
    background-color: #f8f9fa;

    .counter-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .counter-icon {
            width: 70px;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}