@use "../abstracts/globals" as globals;

.services-intro {
    .service-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .service-icon {
            width: 70px;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .list-unstyled {
            li {
                opacity: 0.8;
                transition: all 0.3s ease;

                &:hover {
                    opacity: 1;
                    transform: translateX(5px);
                }
            }
        }

        .btn-theme-outline {
            position: relative;
            z-index: 1;
        }
    }
}  