@use "../abstracts/variables" as variables;

.download-app-4 {
    .blob-background {
        z-index: 0;
        
        .blob {
            border-radius: 50%;
            opacity: 0.3;
            filter: blur(100px);
        }
        
        .blob-1 {
            width: 400px;
            height: 400px;
            top: -100px;
            right: -100px;
        }
        
        .blob-2 {
            width: 300px;
            height: 300px;
            bottom: -100px;
            left: -100px;
        }
    }

    .phone-mockup {
        .blob-phone-bg {
            width: 500px;
            height: 500px;
            background: rgba(variables.$theme-color, 0.1);
            border-radius: 63% 37% 30% 70% / 50% 45% 55% 50%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 0;
            animation: blob-morph 8s ease-in-out infinite;
        }

        .phone-frame {
            z-index: 1;
            max-width: 320px;
            margin: 0 auto;
        }
    }

    @keyframes blob-morph {
        0%, 100% { border-radius: 63% 37% 30% 70% / 50% 45% 55% 50%; }
        50% { border-radius: 40% 60% 70% 30% / 60% 40% 50% 50%; }
    }
}