@use "../abstracts/globals" as globals;

.hero-area-2 {
    background-color: #FFF5E1;
    top: 47px;  
    @include globals.media-breakpoint-between(xs,lg) {
        top: 60px;
      }

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 12px; /* adjust height as needed */
        background-image: url('../images/taxi-stripe.webp');
        background-repeat: repeat-x;
        background-size: auto 100%;
        z-index: 1;
    }
    .swiper-pagination {
        position: relative;
        margin-top: 2rem; // Add space between slides and pagination
        display: flex;
        justify-content: center;
        align-items: center;
    
        &-bullet {
          width: 12px;
          height: 12px;
          background-color: #ddd;
          opacity: 0.5;
          margin: 0 8px; // Space between pagination dots
          transition: all 0.3s ease;
          border-radius: 50%;
    
          &-active {
            width: 16px;
            height: 16px;
            background-color: var(--primary-color, #ffc107); // Use your theme's primary color
            opacity: 1;
          }
        }
    }
}