// Self-Driving Car Hero Section Styles
@use '../abstracts/variables' as *;

.hero-self-driving {
  min-height: 100vh;
  position: relative;
  background-color: $background-light-3;
  overflow: hidden;
  
  // Tech grid background
  .hero-background {
    z-index: 0;
    
    .tech-grid {
      background-image: linear-gradient(rgba(254, 204, 24, 0.05) 1px, transparent 1px),
                        linear-gradient(90deg, rgba(254, 204, 24, 0.05) 1px, transparent 1px);
      background-size: 20px 20px;
      opacity: 0.7;
    }
    
    // Animated sensor waves
    .sensor-wave {
      border-radius: 50%;
      border: 1px solid rgba(254, 204, 24, 0.2);
      
      &-1 {
        width: 300px;
        height: 300px;
        top: 50%;
        right: 10%;
        transform: translate(50%, -50%);
        animation: pulse 4s infinite;
      }
      
      &-2 {
        width: 500px;
        height: 500px;
        top: 50%;
        right: 10%;
        transform: translate(50%, -50%);
        animation: pulse 4s infinite 1s;
      }
      
      &-3 {
        width: 700px;
        height: 700px;
        top: 50%;
        right: 10%;
        transform: translate(50%, -50%);
        animation: pulse 4s infinite 2s;
      }
    }
  }
  
  // Tech badge styling (for stats)
  .tech-badge {
    border-radius: 12px;
    backdrop-filter: blur(5px);
    border: 1px solid rgba(254, 204, 24, 0.2);
  }
  
  // Self-driving car visualization
  .autonomous-car-visual {
    position: relative;
    margin-top: 2rem;
    
    .car-container {
      position: relative;
      border-radius: 16px;
      overflow: hidden;
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
      
      .car-image {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
      
      // Sensor visualization overlay
      .sensor-visualization {
        z-index: 2;
        
        .lidar-scan {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: radial-gradient(circle, transparent 30%, rgba(254, 204, 24, 0.05) 70%);
          animation: lidar-rotate 8s linear infinite;
        }
        
        .object-detection {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          
          .detection-box {
            position: absolute;
            border: none;
            background-color: transparent;
            padding: 0;
            
            // Center dot
            &::after {
              content: '';
              position: absolute;
              width: 12px;
              height: 12px;
              background-color: $theme-color;
              border-radius: 50%;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              box-shadow: 0 0 10px rgba(254, 204, 24, 0.8);
              z-index: 3;
              animation: pulse-dot 2s infinite;
            }
            
            // Connecting lines
            &::before {
              content: attr(data-object);
              position: absolute;
              top: -30px;
              left: 50%;
              transform: translateX(-50%);
              font-size: 12px;
              background-color: rgba(254, 204, 24, 0.9);
              color: $text-dark-color;
              padding: 2px 8px;
              border-radius: 12px;
              font-weight: 500;
              letter-spacing: 0.5px;
              white-space: nowrap;
              box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
              
              // Line connecting label to dot
              &::after {
                content: '';
                position: absolute;
                width: 1px;
                height: 20px;
                background: linear-gradient(to bottom, rgba(254, 204, 24, 0.9), rgba(255, 191, 0, 0.9));
                bottom: -20px;
                left: 50%;
              }
            }
            
            &-1 {
              width: 40px;
              height: 80px;
              top: 40%;
              left: 20%;
              animation: detection-pulse 2s infinite;
            }
            
            &-2 {
              width: 80px;
              height: 50px;
              top: 60%;
              right: 15%;
              animation: detection-pulse 2s infinite 0.7s;
            }
            
            &-3 {
              width: 30px;
              height: 30px;
              top: 30%;
              right: 30%;
              animation: detection-pulse 2s infinite 1.4s;
            }
          }
        }
      }
      
      // AI decision visualization
      .ai-decision {
        position: absolute;
        bottom: 20px;
        left: 20px;
        right: 20px;
        background-color: rgba(59, 59, 59, 0.85);
        border-radius: 8px;
        padding: 10px;
        color: $white;
        z-index: 3;
        backdrop-filter: blur(5px);
        border: 1px solid rgba(254, 204, 24, 0.2);
        
        .decision-path {
          height: 4px;
          background: linear-gradient(to right, $theme-color, $theme-color-darker);
          border-radius: 2px;
          margin-bottom: 10px;
          position: relative;
          
          &::before {
            content: '';
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $theme-color-darker;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            animation: path-progress 3s infinite;
          }
        }
        
        .tech-data {
          display: flex;
          justify-content: space-between;
          
          .data-point {
            display: flex;
            flex-direction: column;
            align-items: center;
            
            .data-label {
              font-size: 10px;
              opacity: 0.7;
              text-transform: uppercase;
            }
            
            .data-value {
              font-size: 14px;
              font-weight: bold;
            }
          }
        }
      }
    }
  }
}

// Animations
@keyframes pulse {
  0% {
    opacity: 0.8;
    transform: translate(50%, -50%) scale(0.8);
  }
  50% {
    opacity: 0.2;
    transform: translate(50%, -50%) scale(1);
  }
  100% {
    opacity: 0.8;
    transform: translate(50%, -50%) scale(0.8);
  }
}

@keyframes lidar-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes detection-pulse {
  0% {
    opacity: 0.8;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 0.8;
  }
}

@keyframes pulse-dot {
  0% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0 0 0 0 rgba(254, 204, 24, 0.7);
  }
  70% {
    transform: translate(-50%, -50%) scale(1.2);
    box-shadow: 0 0 0 10px rgba(254, 204, 24, 0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0 0 0 0 rgba(254, 204, 24, 0);
  }
}

@keyframes path-progress {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

// Responsive adjustments
@media (max-width: 992px) {
  .hero-self-driving {
    .autonomous-car-visual {
      margin-top: 3rem;
      margin-bottom: 2rem;
    }
  }
}

@media (max-width: 768px) {
  .hero-self-driving {
    .sensor-wave {
      display: none;
    }
  }
}
