@use "../abstracts/globals" as globals;

.hero-area-3 {
    background-color: rgba(globals.$theme-color, 0.05);
    .hero-car-mockup {
        .car-image-container {
            position: relative;
            z-index: 1;
        }

        .app-overlay {
            top: 20%;
            right: 10%;
            width: 250px;
            z-index: 2;

            .mobile-app-mockup {
                background: white;
                border-radius: 20px;
                box-shadow: 0 20px 40px rgba(0,0,0,0.1);
                padding: 15px;

                .app-screen {
                    background: #f4f4f4;
                    border-radius: 15px;
                    padding: 10px;

                    .app-header {
                        display: flex;
                        justify-content: space-between;
                        margin-bottom: 10px;
                        color: #333;
                    }

                    .map-placeholder {
                        height: 120px;
                        background: #e0e0e0;
                        border-radius: 10px;
                        margin-bottom: 10px;
                    }

                    .app-ride-details {
                        background: white;
                        padding: 10px;
                        border-radius: 10px;
                        margin-bottom: 10px;

                        .pickup-location, 
                        .dropoff-location {
                            display: flex;
                            align-items: center;
                            margin-bottom: 5px;

                            i {
                                margin-right: 10px;
                                color: globals.$theme-color;
                            }
                        }

                        .ride-price {
                            display: flex;
                            justify-content: space-between;
                            font-weight: bold;
                        }
                    }

                    .btn-confirm-ride {
                        width: 100%;
                        background-color: globals.$theme-color;
                        color: white;
                        border: none;
                        padding: 10px;
                        border-radius: 10px;
                    }
                }
            }
        }
    }
}