@use "../abstracts/globals" as globals;
@use "sass:color";

.package-tiers {
    .bg-gradient-dark {
        background: linear-gradient(145deg, #2c3e50, #1a252f) !important;
        border: none;
        
        &:hover {
            background: linear-gradient(145deg, #34495e, #2c3e50) !important;
        }
    }
    .package-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-10px);
            box-shadow: 0 15px 30px rgba(0,0,0,0.1);
        }

        &.popular-package {
            border: none;
            background: linear-gradient(45deg, globals.$theme-color, color.scale(globals.$theme-color, $lightness: -15%));

            .popular-badge {
                font-weight: 500;
                box-shadow: 0 5px 15px rgba(globals.$warning, 0.3);
            }
        }

        .package-header {
            .badge {
                font-size: 0.9rem;
                padding: 0.5rem 1rem;
            }

            .package-price {
                font-size: 3rem;
                font-weight: 700;
                line-height: 1;

                .currency {
                    font-size: 1.5rem;
                    vertical-align: super;
                }

                .duration {
                    font-size: 1rem;
                    font-weight: 400;
                    opacity: 0.7;
                }
            }
        }

        .package-features {
            .feature-item {
                .icon-box {
                    width: 32px;
                    height: 32px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all 0.3s ease;

                    i {
                        font-size: 0.9rem;
                        transition: all 0.3s ease;
                    }
                }

                &:hover {
                    .icon-box {
                        transform: scale(1.1);

                        i {
                            transform: rotate(15deg);
                        }
                    }
                }
            }
        }
    }

    // Duration Toggle
    .btn-link {
        text-decoration: none;
        transition: all 0.3s ease;

        &.active {
            color: globals.$theme-color-text !important;
            background-color: globals.$theme-color !important;
        }

        &:hover {
            color: globals.$theme-color-text !important;
        }
    }

    // Additional Features
    .additional-features {
        border: 1px solid rgba(0,0,0,0.1);

        .icon-box {
            transition: all 0.3s ease;

            i {
                transition: all 0.3s ease;
            }
        }

        .col-sm-4:hover {
            .icon-box {
                transform: scale(1.1);

                i {
                    transform: rotate(15deg);
                }
            }
        }
    }
}

// Responsive Adjustments
@media (max-width: 991.98px) {
    .package-tiers {
        .package-card {
            margin-bottom: 2rem;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

@media (max-width: 575.98px) {
    .package-tiers {
        .package-header {
            .package-price {
                font-size: 2.5rem !important;

                .currency {
                    font-size: 1.25rem !important;
                }
            }
        }

        .additional-features {
            .col-sm-4 {
                margin-bottom: 2rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
