@use "../abstracts/globals" as globals;
.features-section {
    .card {
        .icon-wrapper {
            position: relative;
            transition: all 0.3s ease;
            
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: globals.$theme-color-darker;
                opacity: 0;
                transition: opacity 0.3s ease;
                z-index: 1;
                border-radius: inherit;
            }
            
            &:hover {
                transform: translateY(-5px);
                
                &::before {
                    opacity: 1;
                }
                
                > i.fas {
                    color: globals.$white !important;
                    position: relative;
                    z-index: 2;
                }
            }
            
            > i.fas {
                transition: color 0.3s ease;
            }
        }
    }
}