@use "../abstracts/globals" as globals;

.booking-section {
    .step-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .step-number {
            position: absolute;
            top: -15px;
            right: -15px;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: bold;
        }

        .step-icon {
            width: 50px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .booking-form {
        border: 1px solid rgba(0,0,0,0.1);
        box-shadow: 0 10px 30px rgba(0,0,0,0.05);

        .form-control, .form-select {
            padding: 0.75rem 1rem;
            font-size: 1rem;

            &:focus {
                box-shadow: none;
                background-color: rgba(globals.$theme-color, 0.05) !important;
            }
        }

        .input-group-text {
            padding: 0.75rem 1rem;
            color: globals.$theme-color;
        }
    }

    .feature-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);

            .feature-icon {
                transform: scale(1.1);
            }
        }

        .feature-icon {
            transition: all 0.3s ease;
            width: 70px;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .blob-radius-2 {
        border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }

    .blob-radius-3 {
        border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .booking-section {
        .step-card {
            .step-number {
                top: -10px;
                right: -10px;
                width: 25px;
                height: 25px;
                font-size: 0.875rem;
            }
        }
    }
}
