input[type=radio] ~ label::before {
  content: "\f111";
  position: absolute;
  font-family: "Font Awesome 6 Pro";
  left: 0;
  top: -1px;
  width: 15px;
  height: 15px;
  font-size: 0.45em;
  line-height: 2em;
  padding-left: 0.5px;
  text-align: center;
  border: 1px solid #fecc18;
  border-radius: 100%;
  font-weight: 700;
  background: #fff;
  color: transparent;
  transition: all 0.2s ease;
}
input[type=radio]:checked ~ label::before {
  border-color: #fecc18;
  background-color: #fecc18;
  color: #fff;
}

label {
  margin-bottom: 0.6em;
  margin-top: -0.3em;
  display: block;
}

