@use "../abstracts/variables" as variables;

.app-download {
    .phone-container {
        position: relative;
        
        .blob-bg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 500px;
            height: 500px;
            background-color: rgba(variables.$theme-color, 0.1);
            border-radius: 63% 37% 30% 70% / 50% 45% 55% 50%;
            z-index: 0;
            animation: blob-animate 8s ease-in-out infinite;
        }
        
        .phone-frame {
            position: relative;
            z-index: 1;
            max-width: 320px;
            margin: 0 auto;
            transform: translateY(-2rem);
        }
        
    }
}

@keyframes blob-animate {
    0% {
        border-radius: 63% 37% 30% 70% / 50% 45% 55% 50%;
    }
    50% {
        border-radius: 40% 60% 70% 30% / 60% 40% 50% 50%;
    }
    100% {
        border-radius: 63% 37% 30% 70% / 50% 45% 55% 50%;
    }
}