@use "../abstracts/globals" as globals;

.testimonial-1 {
  position: relative;
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: center top;
  background-image: url(../../assets/images/home/bg3.webp);
  &:after {
    top: 0;
    left: 0;
    z-index: -1;
    content: '';
    width: 100%;
    height: 50%;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.6);
  } 
  .swiper-pagination {
    position: relative;
    margin-top: 2rem; // Add space between slides and pagination
    display: flex;
    justify-content: center;
    align-items: center;

    &-bullet {
      width: 12px;
      height: 12px;
      background-color: #ddd;
      opacity: 0.5;
      margin: 0 8px; // Space between pagination dots
      transition: all 0.3s ease;
      border-radius: 50%;

      &-active {
        width: 16px;
        height: 16px;
        background-color: var(--primary-color, #ffc107); // Use your theme's primary color
        opacity: 1;
      }
    }

    @include globals.media-breakpoint-down(sm) {
      margin-top: 1.5rem;

      &-bullet {
        width: 10px;
        height: 10px;
        margin: 0 6px;

        &-active {
          width: 14px;
          height: 14px;
        }
      }
    }
  }

  button.prev-btn {
    position: absolute;
    left: -3%;
    top: 40%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    @include globals.media-breakpoint-up(lg) {
      left: 1%;
    }
  }

  button.next-btn {
    position: absolute;
    right: -3%;
    top: 40%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    @include globals.media-breakpoint-up(lg) {
      right: 1%;
    }
  }

  button.prev-btn,
  button.next-btn {
    @include globals.media-breakpoint-up(lg) {
      display: none;
    }
  }
}
