@use '../abstracts/variables' as *;

.download-app-self-driving {
  position: relative;
  
  // Tech background elements
  .tech-background {
    z-index: -1;
    overflow: hidden;
    
    .tech-grid-light {
      background-image: linear-gradient(to right, rgba($theme-color, 0.05) 1px, transparent 1px),
                        linear-gradient(to bottom, rgba($theme-color, 0.05) 1px, transparent 1px);
      background-size: 40px 40px;
      opacity: 0.7;
    }
    
    .tech-circle {
      border-radius: 50%;
      
      &.tech-circle-1 {
        width: 300px;
        height: 300px;
        top: -100px;
        left: -100px;
        opacity: 0.6;
      }
      
      &.tech-circle-2 {
        width: 500px;
        height: 500px;
        bottom: -200px;
        right: -200px;
        opacity: 0.4;
      }
    }
  }
  
  // Phone mockup styling
  .phone-mockup {
    max-width: 400px; // Limit the maximum width
    margin: 0 auto; // Center the phone mockup
    position: relative;
    
    .tech-phone-bg {
      width: 100%;
      height: 100%;
      background: radial-gradient(circle, rgba($theme-color, 0.1) 0%, rgba($theme-color, 0) 70%);
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
    }
    
    .phone-frame {
      position: relative;
      z-index: 1;
      padding: 15px;
      
      img {
        width: 100%;
        height: auto;
        max-width: 100%;
        border-radius: 30px;
        box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
        display: block;
      }
    }
    
    .tech-element {
      box-shadow: 0 0 20px rgba($theme-color, 0.3);
      
      &.tech-element-1 {
        width: 80px;
        height: 80px;
        top: 10%;
        right: 5%;
        animation: float 6s ease-in-out infinite;
      }
      
      &.tech-element-2 {
        width: 120px;
        height: 120px;
        bottom: 15%;
        left: 0;
        animation: float 8s ease-in-out infinite reverse;
      }
    }
  }
  
  // Feature icons
  .bg-warning {
    &.bg-opacity-10 {
      transition: all 0.3s ease;
    }
  }
  
  .d-flex:hover {
    .bg-warning.bg-opacity-10 {
      background-color: rgba($theme-color, 0.2) !important;
      transform: scale(1.05);
    }
  }
  
  // App store buttons
  .btn-dark {
    transition: all 0.3s ease;
    
    &:hover {
      background-color: lighten(#212529, 10%);
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    }
  }
}

// Float animation
@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0);
  }
}
