@use "../abstracts/globals" as globals;
//upper header styles
.upper-header{
    .search {
        .form{
            display: flex;
            input.form-control {
                height: 42px;  
                padding: 8px 15px 8px 46px;  
                margin: 0;
                font-size: 16px;
                font-weight: 600;
                border-radius: 50px;
                color: globals.$background-light-1;
                background-color: globals.$background-dark-3;
            }
        }
    }
    a.btn-link{
        @include globals.media-breakpoint-between(lg, xl) {
            text-align: left;
        }
    }
}
// lower header styles
.navbar-dark {
    .nav-item {

        &.active,
        &:hover {
            .nav-link {
                color: globals.$theme-color; // Use your theme's primary color
            }
        }

        .nav-link {
            &.active {
                color: globals.$theme-color; // Fallback if active class is on the link
            }
        }
    }
    /* DropDown Style - Start */
    .navbar-nav {
        .dropdown-menu {
            width: globals.$dropdown-width;
            background-color: globals.$dropdown-bg;
            border: none;
            border-radius: globals.$dropdown-border-radius;
            box-shadow: globals.$dropdown-box-shadow;
            padding: 0;
            margin-top: 10px;
            transition: globals.$dropdown-transition;

            &::before {
                content: '';
                position: absolute;
                top: -10px;
                right: 60px;
                width: 0;
                height: 0;
                border-left: 10px solid transparent;
                border-right: 10px solid transparent;
                border-bottom: 10px solid globals.$dropdown-bg;
            }

            .dropdown-item {
                color: globals.$dropdown-item-color;
                padding: globals.$dropdown-item-padding;
                transition: globals.$dropdown-transition;
                font-family: globals.$dropdown-font-family;
                font-size: globals.$dropdown-font-size;
                font-weight: globals.$dropdown-font-weight;
                line-height: globals.$dropdown-line-height;
                letter-spacing: globals.$dropdown-letter-spacing;

                &:hover,
                &:focus {
                    background-color: globals.$dropdown-item-hover-bg;
                    color: globals.$dropdown-item-hover-color;
                }

                &.active,
                &:active {
                    background-color: globals.$dropdown-item-active-bg;
                    color: globals.$dropdown-item-hover-color;
                }
            }
        }

        .nav-item.dropdown {
            position: relative;

            &::after {
                content: '';
                position: absolute;
                bottom: -20px;
                left: 0;
                width: 100%;
                height: 20px;
            }

            .dropdown-menu {
                opacity: 0;
                visibility: hidden;
                transform: translateY(-10px);
                transition: opacity 0.3s ease, transform 0.3s ease, visibility 0.3s;
            }

            &:hover {
                .dropdown-menu {
                    display: block;
                    opacity: 1;
                    visibility: visible;
                    transform: translateY(0);
                }
            }
        }

        .dropdown-menu {
            &:hover {
                display: block;
                opacity: 1;
                visibility: visible;
                transform: translateY(0);
            }
        }
    }

    /* DropDown Style - End */
}
