// Offcanvas Style
.offcanvas {
    width: 300px;
    background-color: #1c2027;
    z-index: 1050;
    color: globals.$white; // Light text for dark background
    transition: transform 0.3s ease-in-out;
}

.offcanvas.show {
    transform: translateX(0);
    visibility: visible;
}

// Style for menu items in dark background
.offcanvas-body {
    // Menu links
    .nav-link {
        color: globals.$white;
        transition: color 0.3s ease;

        &:hover {
            color: globals.$theme-color;
        }
    }

    // Icons in menu
    .icon-circle {
        background-color: rgba(255,255,255,0.1); // Subtle background
        
        i {
            color: globals.$white; // White icons
        }

        &:hover {
            background-color: rgba(255,255,255,0.2);
        }
    }

}

// Offcanvas header
.offcanvas-header {
    border-bottom: 1px solid rgba(255,255,255,0.1);
    
    .offcanvas-title {
        color: #fff;
    }
    // Close button
    .btn-close {
        filter: invert(1) grayscale(100%) brightness(200%);
        opacity: 1; // Ensure full visibility

    }
}

// Hamburger Menu Animation
.mobile-nav-toggle{
    float: right;
    & .navbar-toggler-icon{
        & span{
            display: block;
            width: 30px;
            height: 3px;
            background-color: globals.$dark;
            margin: 6px 0 2px;
            transition: all 0.3s ease-in-out;
            &:nth-child(1){
                margin-top: 0;
            }
            &:nth-child(3){
                width: 15px;
                margin-bottom: 0;
            }
        }
    }
    & .active{
        & span{
            &:nth-child(1){
                width: 15px;
            }
            &:nth-child(3){
                width: 30px;
            }
        }
    }
}
