@use "../abstracts/globals" as globals;
.partners-section {
    .partner-card {
        transition: all 0.3s ease;
        border: 1px solid rgba(0,0,0,0.1);
        height: 100%;

        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
        }

        .partner-logo {
            height: 38px;
            object-fit: contain;
            filter: grayscale(1);
            opacity: 0.7;
            transition: all 0.3s ease;
        }

        &:hover .partner-logo {
            filter: grayscale(0);
            opacity: 1;
        }
    }

    .btn-outline-theme {
       

        &:hover, &.active {
            background-color: globals.$theme-color-darker;
            color: white;
        }
    }
}