@use "sass:color";
@use '../abstracts/globals' as globals;

.btn-theme {
  position: relative;
  background-image: linear-gradient(to right, globals.$btn-theme-color 50%, globals.$black 50%);
  background-size: 200% 100%;
  background-position: 0 0;
  color: globals.$black;
  border-color: globals.$btn-theme-color;
  transition: all 0.5s ease;
  text-transform: uppercase;
  overflow: hidden;

  &:hover,
  &:focus {
    color: globals.$white;
    border-color: globals.$btn-theme-color-hover;
    background-position: -100% 0;
  }

  &:active {
    background-color: globals.$btn-theme-color-active !important;
    color: globals.$black !important;
    border-color: globals.$btn-theme-color-active !important;
  }
}

.btn-theme-outline {
  position: relative;
  background-image: linear-gradient(to right, transparent 50%, globals.$black 50%);
  background-size: 200% 100%;
  background-position: 0 0;
  color: globals.$black;
  border-color: globals.$black;
  transition: all 0.5s ease;
  text-transform: uppercase;
  overflow: hidden;
  border-width: 2px;

  &:hover,
  &:focus {
    color: globals.$white;
    border-color: globals.$black;
    background-position: -100% 0;
  }

  &:active {
    background-color: transparent !important;
    color: globals.$black !important;
    border-color: globals.$black !important;
  }

  &.btn-white {
    color: globals.$white;
    border-color: globals.$white;
    background-image: linear-gradient(to right, transparent 50%, globals.$white 50%);

    &:hover,
    &:focus {
      color: globals.$black;
      border-color: globals.$white;
      background-position: -100% 0;
    }

    &:active {
      background-color: transparent !important;
      color: globals.$white !important;
      border-color: globals.$white !important;
    }
  }
}

//navigation button style
.btn-link{
  color: globals.$text-dark-color;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.1s;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    color: globals.$theme-color-darker;
  }
  .icon-circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: globals.$theme-color; // Use your theme color variable
    display: inline-flex;
    align-items: center;
    justify-content: center;

    i {
      color: globals.$text-dark-color; // Dark text color
      font-size: 1.2rem;
    }
    
    &:hover {
        background-color: globals.$theme-color-darker;
      }
  }
}
